<template>
  <div class='main'>
    <heads />
    <van-tabs animated swipeable class="tabs" title-active-color="#1F75D9" title-inactive-color="#333" @change="change">
      <van-tab :title="item.title" v-for="(item, index) in list" :key="index">
        <!-- <van-pull-refresh v-model="list[index].refreshing" @refresh="onRefresh(index)"> -->
        <van-list v-model="list[index].loading" :finished="list[index].finished" :error.sync="list[index].error"
          error-text="请求失败，点击重新加载" finished-text="没有更多了" @load="onLoad(index)">
          <div class="listing" v-for="(item, i) in list[index].items" :key="i">
            <div class="head">
              <div class="type">{{ $util.taskType(item.type) }}</div>
              <div class="title">{{ item.title }}</div>
            </div>
            <div class="info">
              <div class="tips">{{ $util.bidStatus(item.log.type) }}</div>
              <div class="times">{{ $util.getUnixToDate('yyyy-mm-dd hh:mm', item.log.create_ts) }}</div>
            </div>
            <div class="details" @click="seeDetails(item.id)">查看详情</div>
            <div class="status matching" v-if="item.status === 0">匹配中</div>
            <div class="status matching" v-else-if="item.status === 1">服务中</div>
            <div class="status success" v-else-if="item.status === 2">已完成</div>
            <div class="status end" v-else-if="item.status === 3">已暂停</div>
            <div class="status open" v-else-if="item.status === 4">已关闭</div>
          </div>
        </van-list>
        <!-- </van-pull-refresh> -->
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import { taskOwner } from '@/api/request'
export default {
  data () {
    return {
      user_id: localStorage.getItem('userId'),
      DateNow: new Date(new Date().toLocaleDateString()).getTime(),
      cursor: '',
      list: [
        {
          title: '全部',
          status: -1,
          items: [],
          refreshing: false,
          loading: false,
          error: false,
          finished: false,
        },
        {
          title: '匹配中',
          status: 0,
          items: [],
          refreshing: false,
          loading: false,
          error: false,
          finished: false,
        },
        {
          title: '服务中',
          status: 1,
          items: [],
          refreshing: false,
          loading: false,
          error: false,
          finished: false,
        },
        {
          title: '已完成',
          status: 2,
          items: [],
          refreshing: false,
          loading: false,
          error: false,
          finished: false,
        }
      ]
    }
  },
  methods: {
    change () {
      this.cursor = ''
    },
    async onLoad (index) {
      const list = this.list[index];
      list.loading = true;
      let res = await taskOwner({
        cursor: this.cursor,
        user_id: this.user_id,
        status: list.status
      })
      if (list.refreshing) {
        list.items = [];
        list.refreshing = false;
      }
      if (res.data.length) {
        res.data.forEach(item => {
          if ((item.begin_time + 86400000) < this.DateNow) {
            item.my_status = 3
            item.status = 3
          }
        })
      }

      list.items = list.items.concat(res.data)
      list.loading = false;
      list.refreshing = false;
      if (res.data.length === 0) {
        list.finished = true;
      } else {
        this.cursor = res.data[res.data.length - 1].create_ts
      }
      console.log(list.items, res)
    },
    onRefresh (index) {
      this.list[index].finished = false;
      onLoad(index);
    },
    seeDetails (id) {
      this.$router.push({
        path: 'demandDetails',
        query: {
          id: id
        }
      })
    }
  }
}
</script>
<style lang='less' scoped>
.tabs {
  :deep(.van-tabs__wrap) {
    height: 47px;

    .van-tabs__nav {
      background-color: transparent;
      padding-bottom: 6px;

      .van-tabs__line {
        width: 18px;
        height: 2px;
        background-color: #1F75D9;
      }
    }
  }
}

.listing {
  margin: 0 10px;
  display: flex;
  flex-direction: column;
  position: relative;
  background: #FFFFFF;
  border-radius: 10px;
  padding: 10px;
  padding-top: 20px;
  margin-bottom: 5px;
  position: relative;

  .serviceStatus {
    position: absolute;
    bottom: 18px;
    left: 105px;

    .icon {
      width: 81px;
      height: 77px;
    }
  }

  .status {
    position: absolute;
    right: 0;
    top: 0;
    height: 18px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 20px;
    text-align: center;
    line-height: 18px;
    font-size: 12px;
    transform: scale(0.917);
    font-weight: 500;
    color: #FFFFFF;
    padding-left: 18px;
    padding-right: 13px;
  }

  .head {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    .type {
      height: 20px;
      background: rgba(31, 117, 217, .2);
      border-radius: 10px;
      padding: 0 10px;
      font-size: 12px;
      font-weight: 500;
      color: #1F75D9;
      text-align: center;
      line-height: 20px;
      margin-right: 13px;
    }

    .title {
      font-size: 14px;
      font-weight: 500;
      color: #333333;
    }
  }

  .info {
    display: flex;
    justify-content: space-between;
    height: 40px;
    background: rgba(31, 117, 217, .1);
    border-radius: 3px;
    align-items: center;
    padding-left: 13px;
    padding-right: 11px;
    margin-bottom: 20px;

    div {
      font-size: 12px;
      font-weight: 400;
      color: #1F75D9;
    }
  }

  .details {
    margin-left: auto;
    height: 34px;
    background: #FFFFFF;
    border: 1px solid #1F75D9;
    border-radius: 17px;
    font-size: 14px;
    line-height: 34px;
    text-align: center;
    padding: 0 14px;
    font-weight: 400;
    color: #1F75D9;
  }

  .matching {
    background: #1F75D9;
  }

  .success {
    background: #00B78C;
  }

  .end {
    background: #909CBA;
  }

  .open {
    background: #FFB22C;
  }
}
</style>